import React, { useState } from "react";
import {
  TextField,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  Button,
  Container,
  Box,
  Typography,
} from "@mui/material";
import MainLayout from "../components/MainLayout.js";
import backgroundImage from "../assets/backgroundHome.png"; // Importa a imagem

export default function CadastroPosto() {
  const [formData, setFormData] = useState({
    razaosocial: "",
    cnpj: "",
    cep: "",
    uf: "",
    cidade: "",
    logradouro: "",
    numero: "",
    status: "",
    usuario: "",
    email: "",
    phone: "",
  });

  const [errors, setErrors] = useState({});

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const validate = () => {
    const newErrors = {};
    if (!formData.razaosocial.trim())
      newErrors.razaosocial = "Razo social  obrigatrio.";
    if (!formData.cnpj.trim()) newErrors.cnpj = "CNPJ  obrigatrio.";
    if (!formData.cep.trim()) newErrors.cep = "CEP  obrigatrio.";
    if (!formData.uf.trim()) newErrors.uf = "UF  obrigatrio.";
    if (!formData.cidade.trim()) newErrors.cidade = "Cidade  obrigatrio.";
    if (!formData.logradouro.trim())
      newErrors.logradouro = "Logradouro  obrigatrio.";
    if (!formData.numero.trim()) newErrors.numero = "Nmero  obrigatrio.";
    if (!formData.status) newErrors.status = "Status  obrigatrio.";
    if (!formData.usuario.trim()) newErrors.usuario = "Usurio  obrigatrio.";
    if (!formData.email.trim() || !/\S+@\S+\.\S+/.test(formData.email))
      newErrors.email = "E-mail invlido.";
    if (!formData.phone.trim() || !/^\d{10,15}$/.test(formData.phone))
      newErrors.phone = "Telefone invlido (apenas nmeros, 10-15 dgitos).";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      console.log("Formulrio enviado com sucesso!", formData);
      alert("Posto cadastrado com sucesso!");
    }
  };

  const handleCancel = () => {
    setFormData({
      razaosocial: "",
      cnpj: "",
      cep: "",
      uf: "",
      cidade: "",
      logradouro: "",
      numero: "",
      status: "",
      usuario: "",
      email: "",
      phone: "",
    });
    setErrors({});
  };

  return (
    <MainLayout>
      <Box
        sx={{
          backgroundImage: `url(${backgroundImage})`,
          //backgroundSize: "cover", // Estica a imagem de background para ocupa todo espao.
          //backgroundPosition: "center", // Centraliza a imagem do background.
          //backgroundRepeat: "no-repeat", // Deixa apenas uma imagem, sem repeti-la.
          backgroundColor: "rgba(255, 255, 255, 0.8)", // Cor branca com transparncia.
          backgroundBlendMode: "overlay", // Mistura o background transparente com a imagem.
          padding: 3,
          borderRadius: 2,
          boxShadow: 3,
        }}
      >
        <Container maxWidth="md">
          <Box
            sx={{
              p: 4,
              bgcolor: "background.paper",
              borderRadius: 2,
              boxShadow: 3,
              //backgroundColor: "#FFFFFF00", // Deixar fundo transparente.
            }}
          >
            <Typography variant="h4" component="h1" gutterBottom align="center">
              Cadastro de Posto
            </Typography>
            <form onSubmit={handleSubmit}>
              <TextField
                fullWidth
                label="Razo social"
                name="razaosocial"
                value={formData.razaosocial}
                onChange={handleInputChange}
                error={!!errors.razaosocial}
                helperText={errors.razaosocial}
                margin="normal"
              />
              <TextField
                fullWidth
                label="CNPJ"
                name="cnpj"
                value={formData.cnpj}
                onChange={handleInputChange}
                error={!!errors.cnpj}
                helperText={errors.cnpj}
                margin="normal"
              />
              {/* ToDo: Criar um component Endereo */}
              <Box
                sx={{
                  bgcolor: "background.paper",
                  borderRadius: 2,
                  boxShadow: 3,
                  padding: "10px",
                  margin: "16px 0 8px 0",
                }}
              >
                <Typography
                  variant="subtitle1"
                  component="h1"
                  gutterBottom
                  align="justify"
                >
                  Dados de Endereo
                </Typography>
                <Box sx={{ display: "flex", flexDirection: "row" }}>
                  <TextField
                    fullWidth
                    label="Cidade"
                    name="cidade"
                    value={formData.cidade}
                    onChange={handleInputChange}
                    error={!!errors.cidade}
                    helperText={errors.cidade}
                    margin="normal"
                    maxWidth="40%"
                    sx={{ marginRight: "20px" }}
                  />

                  <TextField
                    fullWidth
                    label="UF"
                    name="uf"
                    value={formData.uf}
                    onChange={handleInputChange}
                    error={!!errors.uf}
                    helperText={errors.uf}
                    margin="normal"
                    maxWidth="40%"
                  />
                </Box>

                <TextField
                  fullWidth
                  label="Logradouro"
                  name="logradouro"
                  value={formData.logradouro}
                  onChange={handleInputChange}
                  error={!!errors.logradouro}
                  helperText={errors.logradouro}
                  margin="normal"
                />

                <Box sx={{ display: "flex", flexDirection: "row" }}>
                  <TextField
                    fullWidth
                    label="CEP"
                    name="cep"
                    value={formData.cep}
                    onChange={handleInputChange}
                    error={!!errors.cep}
                    helperText={errors.cep}
                    margin="normal"
                    maxWidth="40%"
                    sx={{ marginRight: "20px" }}
                  />

                  <TextField
                    fullWidth
                    label="Nmero"
                    name="numero"
                    value={formData.numero}
                    onChange={handleInputChange}
                    error={!!errors.numero}
                    helperText={errors.numero}
                    margin="normal"
                  />
                </Box>
              </Box>

              <TextField
                fullWidth
                label="E-mail"
                name="email"
                value={formData.email}
                onChange={handleInputChange}
                error={!!errors.email}
                helperText={errors.email}
                margin="normal"
              />

              <TextField
                fullWidth
                label="Telefone"
                name="phone"
                value={formData.phone}
                onChange={handleInputChange}
                error={!!errors.phone}
                helperText={errors.phone}
                margin="normal"
              />

              <TextField
                fullWidth
                label="Usurio"
                name="usurario"
                value={formData.usuario}
                onChange={handleInputChange}
                error={!!errors.usuario}
                helperText={errors.usuario}
                margin="normal"
              />

              <FormControl component="fieldset" margin="normal">
                <FormLabel component="legend">Status</FormLabel>
                <RadioGroup
                  row
                  name="status"
                  value={formData.status}
                  onChange={handleInputChange}
                >
                  <FormControlLabel
                    value="Ativo"
                    control={<Radio />}
                    label="Ativo"
                  />
                  <FormControlLabel
                    value="Inativo"
                    control={<Radio />}
                    label="Inativo"
                  />
                </RadioGroup>
                {errors.status && (
                  <Typography color="error" variant="body2">
                    {errors.status}
                  </Typography>
                )}
              </FormControl>

              <Box
                sx={{ mt: 3, display: "flex", justifyContent: "space-between" }}
              >
                <Button type="submit" variant="contained" color="primary">
                  Cadastrar
                </Button>
                <Button
                  type="button"
                  variant="outlined"
                  color="secondary"
                  onClick={handleCancel}
                >
                  Cancelar
                </Button>
              </Box>
            </form>
          </Box>
        </Container>
      </Box>
    </MainLayout>

    // <MainLayout>
    //   <Box>
    //     <Typography>Hello Word!</Typography>
    //     <Typography>Cadastro de Posto</Typography>
    //   </Box>
    // </MainLayout>
  );
}
